import Rete from "rete";
import { TextControl } from "../Controls/TextControl";
import { strSocket } from "../Sockets/Sockets";
import { MyNode } from "./MyNode";

export class MidjorneyNode extends Rete.Component {
  constructor() {
    super("Midjorney");
    this.data.component = MyNode; // optional
  }

  builder(node) {
    let inp1 = new Rete.Input("text1", "Text", strSocket);
    let out = new Rete.Output("text", "Text", strSocket);

    inp1.addControl(new TextControl(this.editor, "text1", node));

    return node
      .addInput(inp1)
      .addControl(new TextControl(this.editor, "preview", node, true))
      .addOutput(out);
  }

  worker(node, inputs, outputs) {
    outputs["text"] = node.data.text;
  }
}
