import React, { useEffect, useState } from "react";
import { editor, components } from "../rete";
import AreaPlugin from "rete-area-plugin";

export default function Sidebar() {
  const [items, setItems] = useState([]);

  useEffect(() => {
    // editor.on("nodecreated connectioncreated", async () => {
    setItems(components);
    // });
  }, []);

  const addNodeHandler = (index) => (el) => {
    el.preventDefault();

    const lastNodePosition = editor.nodes[editor.nodes.length - 1].position;
    const newPosition = lastNodePosition.map((el, index) => {
      if (index === 0) {
        return el + 220;
      } else {
        return el;
      }
    });

    components[index].createNode().then((component) => {
      component.position = newPosition;
      editor.addNode(component);

      editor.view.resize();
      AreaPlugin.zoomAt(editor);
    });
  };

  return (
    <div className="sidebar">
      {items && (
        <ul>
          {items.map((item, index) => (
            <li key={index}>
              <button onClick={addNodeHandler(index)}> {item.name}</button>
            </li>
          ))}
        </ul>
      )}
    </div>
  );
}
